.App {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.Form {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  width: 600px;
  height: 300px;
  padding: 50px;
}

input {
  width: 400px;
  height: 20px;
}

.checked {
  color: orange;
}

textarea {
  width: 400px;
  height: 80px;
}

.latest-comments-box {
  border-style: solid;
  border-width: 1px;
  width: 100%;
  height: 300px;
  padding: 15px;
}

.comment-row {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

.comment-text {
  margin: 0;
}

.app-graph {
  -ms-flex-pack: center;
      justify-content: center;
  width: 600px;
  height: 300px;
  border-style: solid;
  border-width: 1px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}



